@media screen and (max-width: 1440px) {
  //NAVBAR
  nav ul li .fa-solid {
    font-size: $desktop-m;
  }
}

@media screen and (max-width: 1080px) {
  //GLOBAL
  section {
    padding: 2em;
  }
  //FONTS
  h1 {
    font-size: $mobile-xl;
  }
  h2 {
    font-size: $mobile-l;
  }
  h3 {
    font-size: $mobile-m;
  }
  p,
  label {
    font-size: $mobile-s;
  }
  .date {
    font-size: $mobile-xs;
    font-style: italic;
  }
  .tag {
    font-size: $mobile-xs;
    text-transform: uppercase;
  }
  //NAVBAR
  nav ul li{
    width: 10%;
  }
  //LOBBY
  .lobby {
    &__content {
      h2 {
        font-size: $mobile-m;
      }
      img {
        width: 35%;
      }
    }
  }
  //WORK
  .work {
    &__list {
        flex-direction: column;
    }
    &__element{
        width: 100%;
        height: 30%;
        img{
          display: none;
        }
    }
    &__details{
        padding: 1em;
        opacity: 0.95;
    }
    h3{
        font-size: $mobile-xl;
    }
  }
  //EXPERIENCE
  .experience{
    &__list{
        gap: 2em;
    }
    &__element{
        flex-direction: column;
        align-items: unset;
        .date{
            width: 100%;
            text-align: end;
        }
    }
    &__name{
        width: 100%;
    }
  }
  //SKILLS
  .skills{
    &__list{
        margin: 0;
        height: 75%;
        flex-wrap: wrap;
        gap: 1;
    }
    &__element{
        padding: 0.5em;
        width: 25%;
        img{
            width: 50%;
        }
    }
  }
  //CONTACT
  .contact{
    &__content{
        height: 80%;
        margin: 0;
        flex-direction: column;
        justify-content: center;
        img{
            display: none;
        }
    }
    &__form{
        width: 100%;
        gap: 1em;
    }
    &__input{
        text-align: start;
        flex-direction: column;
        input,
        textarea {
            width: 100%;
        }
    }
    button{
        align-self: center;
    }
  }
}
@media screen and (max-width: 600px) {
  //GLOBAL
  .tag {
    font-size: $mobile-xxs;
    text-transform: uppercase;
  }
  //NAVBAR
  nav ul li {
    width: 20%;
    .fa-solid {
      font-size: $mobile-l;
    }
  } 
  //SKILLS
    .skills{
        &__element{
            img{
                width: 75%;
            }
        }
      }
}
