.work {
  &__list {
    margin: auto;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 75%;
  }
  &__element {
    width: 30%;
    box-shadow: 0 0 1em 0.25em var(--color-dark);
    display: flex;
    border-radius: 1em;
    transform: translateY(150%);
    opacity: 0;
    a {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      img {
        z-index: 2;
        width: 100%;
        height: 100%;
        border-radius: 1em;
        object-fit: fill;
        transition: opacity 250ms;
        &:hover {
          opacity: 0.05;
        }
      }
    }
  }
  &__details {
    position: absolute;
    border-radius: 1em;
    padding: 2em;
    background-color: #323232;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h3 {
      color: #d9d9d9;
      font-size: $desktop-xl;
      text-shadow: 0 0 0.5em #d9d9d9;
    }
    p {
      color: #d9d9d9;
      text-shadow: 0 0 0.5em #d9d9d9;
    }
  }
  &__tagsList {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }
  .tag {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25%;
    background-color: #d9d9d9;
    color: #323232;
    padding: 0 0.25em;
    border-radius: 1em;
    box-shadow: 0 0 1em 0.25em #d9d9d9;
  }
}
.dark .work img{
  filter: brightness(50%);
}